<template>
<section id="our-history" class="bg-light-gray">
  <b-container>
    <b-row>
      <b-col class="text-center">
        <h2 class="section-heading">Nossa Historia</h2>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="col-lg-12 couple-bg">
        <b-col cols="2"></b-col>
        <b-col>
          <div class="intro-body">
            <p><b>Q</b>uando a Érica veio fazer o mestrado em São Carlos, resolveu realizar um sonho antigo que era aprender a tocar taiko. Na época o Marcel era um dos líderes do grupo Yanagi Taiko, mas estava em um período de viagens à trabalho e aparecia com menos frequência nos treinos. Quem deu a letra foi o primo do Marcel, o Shinji (Gui): "Ó Marcel, acho que tem um bom partido lá no Taiko, hein. Da sua idade, legal, alta... Quem sabe, né?!"</p>
            <p><b>N</b>ão foi amor à primeira vista. Nem na segunda, nem terceira... A Érica lembra do Marcel como líder: 'cara sério e que trabalha demais' e ele lembra dela assim: 'pessoa muito independente que nem pensa em alguém se relacionar com um cara como o Marcel, que trabalha demais...'</p>
            <p><b>A</b>lguns meses depois começaram a trocar mensagens no celular, até que a <i>friendzone</i> evoluiu para (vários) jantares e um beijo no aniversário do Marcel!</p>
            <p><b>D</b>esde então evoluíram bastante, entre estudos e trabalho, batalharam para apoiar um ao outro! O casamento foi um dos momentos mais importantes na vida do casal, principalmente ao lado de amigos e familiares tão especiais!</p>
          </div>
        </b-col>
        <b-col cols="2"></b-col>
      </b-col>
    </b-row>
    <!-- <b-row><b-col class="text-center">Confira abaixo mais detalhes sobre o casamento</b-col></b-row> -->
  </b-container>
</section>
</template>

<script>
export default {
}
</script>

<style lang="css" scoped>
  #our-history {
    background-color: #d2f9be;
    color: #333;
  }

  section {
    font-size: 1.5em;
    padding: 30px 0 60px 0;
  }

  section h2.section-heading {
    /*.script-font();*/
    font-size: 41px;
    margin-top: 0;
    margin-bottom: 70px;
  }

  section h3.section-subheading {
    font-size: 16px;
    /*.serif-font;*/
    text-transform: none;
    font-style: italic;
    font-weight: 400;
    margin-bottom: 75px;
  }


</style>



